<template>
  <div class="q-gutter-y-lg">
    <q-card flat bordered class="rounded">
      <q-card-section
        :class="$q.screen.gt.sm ? 'q-px-lg q-py-md' : 'q-px-md q-py-sm'"
      >
        <q-toolbar class="text-primary q-pa-none">
          <q-toolbar-title class="text-h6 font-medium">
            Account Information
          </q-toolbar-title>

          <div class="q-gutter-sm">
            <DynamicButton
              v-if="!editing"
              label="Edit Info"
              icon="icon-pencil-02"
              @click="editing = true"
            />

            <DynamicButton
              v-if="editing"
              outline
              :flat="$q.screen.lt.xs || $q.screen.xs"
              label="Cancel"
              icon="icon-x-close"
              @click="cancelEditing"
            />

            <DynamicButton
              v-if="editing"
              label="Save Changes"
              icon="icon-save-01"
              @click="saveAccountInfo"
            />
          </div>
        </q-toolbar>
      </q-card-section>
      <q-separator />
      <q-card-section
        class="q-col-gutter-md font-normal text-primary text-body2"
        :class="$q.screen.gt.sm ? 'q-pa-lg' : 'q-pa-md'"
        :style="editing ? 'max-width: 900px' : ''"
      >
        <div class="row full-wdith items-center justify-start">
          <div class="col-12 col-sm-4 col-md-3 font-medium color-primary2">
            Full Name<span v-if="editing" class="text-red">*</span>
          </div>
          <div v-if="editing" class="col-12 col-sm-8 col-md-9">
            <div class="row q-col-gutter-sm items-center justify-start">
              <div class="col-12 col-md-6">
                <q-input
                  dense
                  outlined
                  placeholder="First Name"
                  v-model="account.firstname"
                  @change="changes_made = true"
                />
              </div>
              <div class="col-12 col-md-6">
                <q-input
                  dense
                  outlined
                  placeholder="Last Name"
                  v-model="account.lastname"
                  @change="changes_made = true"
                />
              </div>
            </div>
          </div>
          <div v-else class="col-12 col-sm-8 col-md-9 letter-space-normal">
            <div>{{ user.firstname }} {{ user.lastname }}</div>
          </div>
        </div>
        <div class="row full-wdith items-center justify-start">
          <div class="col-12 col-sm-4 col-md-3 font-medium color-primary2">
            Email Address<span v-if="editing" class="text-red">*</span>
          </div>
          <div class="col-12 col-sm-8 col-md-9">
            <q-input
              v-if="editing"
              dense
              outlined
              placeholder="example@email.com"
              v-model="account.email"
              @change="changes_made = true"
            />
            <div v-else class="letter-space-normal">
              <a
                :href="'mailto:' + user.email"
                target="_blank"
                class="text-primary"
              >
                {{ user.email }}
              </a>
            </div>
          </div>
        </div>
        <div
          v-if="editing || (user.phone_country_code && user.phone_number)"
          class="row full-wdith items-center justify-start"
        >
          <div class="col-12 col-sm-4 col-md-3 font-medium color-primary2">
            Phone Number<span v-if="editing" class="text-red"></span>
          </div>
          <div class="col-12 col-sm-8 col-md-9">
            <InputPhoneNumber
              v-if="editing"
              @areaCodeChanged="
                (e) => {
                  account.phone_country_code = e;
                  if (e != user.phone_country_code) changes_made = true;
                }
              "
              @numberChanged="
                (e) => {
                  account.phone_number = e;
                  if (e != user.phone_number) changes_made = true;
                }
              "
              :areaCode="account.phone_country_code"
              :phoneNumber="account.phone_number"
            />
            <div v-else class="letter-space-normal">
              <external-link
                :url="`tel:+${user.phone_country_code}${user.phone_number}`"
                :label="`+${user.phone_country_code} ${user.phone_number}`"
              />
            </div>
          </div>
        </div>
        <div v-if="!editing" class="row full-wdith items-center justify-start">
          <div class="col-12 col-sm-4 col-md-3 font-medium color-primary2">
            Company
          </div>
          <div class="col-12 col-sm-8 col-md-9">
            <div class="letter-space-normal">{{ selectedOrg.title }}</div>
          </div>
        </div>
        <div
          v-if="!editing && !isOrgOwner()"
          class="row full-wdith items-center justify-start"
        >
          <div class="col-12 col-sm-4 col-md-3 font-medium color-primary2">
            Role
          </div>
          <div class="col-12 col-sm-8 col-md-9">
            <div class="letter-space-normal">
              {{ userRoles[user.role]?.label || user.role || 'Unknown' }}
            </div>
          </div>
        </div>
      </q-card-section>
    </q-card>

    <q-card v-if="editing" flat bordered class="rounded">
      <form
        autocorrect="off"
        autocapitalize="off"
        autocomplete="off"
        spellcheck="false"
      >
        <q-card-section
          :class="$q.screen.gt.sm ? 'q-px-lg q-py-md' : 'q-px-md q-py-sm'"
        >
          <q-toolbar class="text-primary q-pa-none">
            <q-toolbar-title class="text-h6 font-medium">
              Account Security
            </q-toolbar-title>

            <div class="q-gutter-sm">
              <DynamicButton
                label="Update Password"
                icon="icon-save-01"
                @click="updatePwd"
                color="primary"
              />
            </div>
          </q-toolbar>
        </q-card-section>
        <q-separator />
        <q-card-section
          class="q-col-gutter-md"
          :style="editing ? 'max-width: 900px' : ''"
          :class="$q.screen.gt.sm ? 'q-px-lg q-py-md' : 'q-px-md q-py-sm'"
        >
          <div class="row full-width items-center justify-start">
            <div class="col-12 col-sm-4 col-md-3 font-medium color-primary2">
              Old Password<span v-if="editing" class="text-red">*</span>
            </div>
            <div class="col-12 col-sm-8 col-md-9">
              <InputPassword
                v-if="editing"
                v-model="account.password_old"
                tabindex="1"
              />
              <div v-else class="letter-space-normal">*******</div>
            </div>
          </div>
          <div class="row full-width items-center justify-start">
            <div class="col-12 col-sm-4 col-md-3 font-medium color-primary2">
              New Password<span v-if="editing" class="text-red">*</span>
            </div>
            <div class="col-12 col-sm-8 col-md-9">
              <InputPassword
                v-if="editing"
                v-model="account.password_new"
                tabindex="2"
              />
              <div v-else class="letter-space-normal">*******</div>
            </div>
          </div>
          <div class="row full-width items-center justify-start">
            <div class="col-12 col-sm-4 col-md-3 font-medium color-primary2">
              Confirm New Password<span v-if="editing" class="text-red">*</span>
            </div>
            <div class="col-12 col-sm-8 col-md-9">
              <InputPassword
                v-if="editing"
                v-model="account.password_conf"
                tabindex="3"
              />
              <div v-else class="letter-space-normal">*******</div>
            </div>
          </div>
        </q-card-section>
      </form>
    </q-card>

    <!-- Dialog Unsaved Edit -->
    <q-dialog v-model="unsaved">
      <dynamic-dialog-confirmation
        v-bind="$root.messagesDialogs?.editingUnsaved"
        @okay="editing = false"
      />
    </q-dialog>
  </div>
</template>

<script>
import { defineComponent } from 'vue';
import {
  extractErrorMessageString,
  objectCopy,
} from 'src/assets/scripts/functions';
import DynamicButton from 'src/components/Helpers/DynamicButton.vue';
import InputPassword from 'src/components/Helpers/InputPassword.vue';
import InputPhoneNumber from 'src/components/Helpers/InputPhoneNumber.vue';
import { mapActions, mapState } from 'pinia';
import useUser from 'stores/user';
import useGrowmodo from 'stores/growmodo';

export default defineComponent({
  name: 'AccountProfile',

  components: { DynamicButton, InputPassword, InputPhoneNumber },

  data() {
    return {
      unsaved: false,
      editing: false,
      account: {},
      changes_made: false,
    };
  },

  computed: {
    ...mapState(useUser, ['user', 'selectedOrg']),
    ...mapState(useGrowmodo, ['userRoles']),
  },

  watch: {
    editing: {
      async handler(val) {
        if (val) {
          // Editing
          // Update the editing value
          this.account = objectCopy(this.user || {});
        } else {
          // Viewing
          // Reset company info
          this.account = {};
          this.changes_made = false;
        }
      },
    },
  },

  methods: {
    ...mapActions(useUser, ['updateAccount', 'updatePassword', 'isOrgOwner']),

    async saveAccountInfo() {
      if (!this.changes_made) {
        this.editing = false;
        // this.$q.notify({
        //   icon: 'icon-check-circle-broken',
        //   message: 'No changes has been made',
        // });
        return;
      }
      const _ = this.account || {};
      const payload = {
        firstname: _.firstname,
        lastname: _.lastname,
        email: _.email,
        phone_number: _.phone_number,
        phone_country_code: _.phone_country_code,
      };
      if (
        (_.phone_number && !_.phone_country_code) ||
        (!_.phone_number && _.phone_country_code)
      ) {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: _.phone_number
            ? 'Phone country code is required'
            : 'Phone number is required',
        });
        return;
      }
      if (_.phone_number) payload.phone_number = _.phone_number;
      if (_.phone_country_code)
        payload.phone_country_code = _.phone_country_code;
      const response = await this.updateAccount(payload);
      if (response.success) {
        this.editing = false;
        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: 'Your changes have been successfully saved',
        });
      } else {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: 'Failed to update profile',
        });
      }
    },

    async updatePwd() {
      let status = {};
      switch (true) {
        case !this.account.password_old:
          status.icon = 'icon-alert-triangle';
          status.iconColor = 'negative';
          status.message = 'Please provide your old password to verify';
          break;

        case !this.account.password_new:
          status.icon = 'icon-alert-triangle';
          status.iconColor = 'negative';
          status.message = 'Please provide your new password';
          break;

        case !this.account.password_conf:
          status.icon = 'icon-alert-triangle';
          status.iconColor = 'negative';
          status.message = 'Please confirm your new password';
          break;

        case this.account.password_new !== this.account.password_conf:
          status.icon = 'icon-alert-triangle';
          status.iconColor = 'negative';
          status.message = 'The password you entered did not match';
          break;

        default:
          status = true;
          break;
      }
      if (status === true) {
        const payload = {
          old_password: this.account.password_old,
          new_password: this.account.password_new,
        };
        const response = await this.updatePassword(payload);
        if (response.success) {
          this.account.password_old = '';
          this.account.password_new = '';
          this.account.password_conf = '';
          this.$q.notify({
            icon: 'icon-check-circle-broken',
            message: 'Your password have been successfully updated',
          });
        } else {
          const message = extractErrorMessageString(response?.message);
          this.$q.notify({
            icon: 'icon-alert-triangle',
            iconColor: 'negative',
            message: message || 'Error updating password',
          });
        }
      } else if (status.message) this.$q.notify(status);
    },

    cancelEditing() {
      if (this.changes_made) {
        this.unsaved = true;
      } else {
        this.editing = false;
      }
    },
  },
});
</script>
