<template>
  <div class="q-gutter-y-lg">
    <q-card flat bordered class="rounded">
      <q-card-section
        :class="$q.screen.gt.sm ? 'q-px-lg q-py-md' : 'q-px-md q-py-sm'"
      >
        <q-toolbar class="text-primary q-pa-none">
          <q-toolbar-title class="text-h6 font-medium">
            Your Current Plan
          </q-toolbar-title>

          <div
            v-if="isOrgOwner() || isOrgAdmin() || isOrgBiller()"
            class="q-gutter-sm"
          >
            <DynamicButton
              v-if="!editing"
              label="Edit Subscription"
              icon="icon-pencil-02"
              @click="editing = true"
            />

            <DynamicButton
              v-if="editing"
              :flat="$q.screen.lt.xs || $q.screen.xs"
              label="Cancel Subscription"
              icon="icon-x-close"
              color="negative"
              @click="startOffBoarding = true"
            />

            <DynamicButton
              v-if="editing"
              :flat="$q.screen.lt.xs || $q.screen.xs"
              label="Cancel Changes"
              icon="icon-x-close"
              @click="editing = false"
            />
          </div>
        </q-toolbar>
      </q-card-section>
      <q-separator />
      <q-card-section :class="$q.screen.gt.sm ? 'q-pa-lg' : 'q-pa-md'">
        <div class="row items-center q-col-gutter-md q-py-sm">
          <div class="col-12 col-md-4 text-secondary text-h6">
            {{ subscription.label }}
            {{ talentCount.id }}
          </div>
          <div class="col-12 col-md-8">
            <div class="q-pb-sm" style="max-width: 350px">
              <RemainingDaysProgress />
            </div>
            <div class="text-primary text-body2">
              {{ subscription.description_1 }}
              {{
                talentCount.value === -1
                  ? 'custom'
                  : numberToWords(talentCount.value)
              }}
              {{ talentCount.value > 1 ? 'users' : 'user' }}.
            </div>
          </div>
        </div>
      </q-card-section>
    </q-card>

    <q-card v-if="editing" flat bordered class="rounded">
      <q-card-section :class="$q.screen.gt.sm ? 'q-pa-lg' : 'q-pa-md'">
        <SubscriptionPlans
          :nextButton="true"
          @updated="planUpdated"
          @nextBtnClick="nextBtnClick"
        />
      </q-card-section>
    </q-card>

    <!-- Offboarding -->
    <q-dialog persistent v-model="startOffBoarding">
      <OffBoarding
        class="rounded"
        style="width: 1080px; max-width: 80vw"
        @exit="
          () => {
            startOffBoarding = false;
          }
        "
      />
    </q-dialog>
  </div>
</template>

<script>
import { defineComponent } from 'vue';
import DynamicButton from 'src/components/Helpers/DynamicButton.vue';
import SubscriptionPlans from 'src/components/Helpers/Subscription/SubscriptionPlans.vue';
import { mapActions, mapState } from 'pinia';
import useUser from 'stores/user';
import useGrowmodo from 'stores/growmodo';
import RemainingDaysProgress from 'src/components/Helpers/Subscription/RemainingDays-Progress.vue';
import OffBoarding from 'src/components/Dialogs/OffBoarding.vue';
import { numberToWords } from 'src/assets/scripts/functions';
import fnStore from 'src/stores/fn.store';

export default defineComponent({
  name: 'SubscriptionInformation',

  components: {
    DynamicButton,
    SubscriptionPlans,
    RemainingDaysProgress,
    OffBoarding,
  },

  data() {
    return {
      editing: false,
      numberToWords,
      startOffBoarding: false,
    };
  },

  computed: {
    ...mapState(useUser, ['user', 'selectedOrg']),
    ...mapState(useGrowmodo, ['subscription', 'subscription_Talents']),

    orgsubscription() {
      return !fnStore.getAuth() ? {} : this.selectedOrg?.subscriptions;
    },

    talentCount() {
      return (
        this.subscription_Talents[
          this.orgsubscription.subscription_talent_id
        ] || {}
      );
    },
  },

  methods: {
    ...mapActions(useUser, ['isOrgOwner', 'isOrgAdmin', 'isOrgBiller']),

    nextBtnClick() {
      this.editing = false;
      this.$root.dialogRequestUpgrade = true;
    },

    planUpdated(planinfo) {
      if (process.env.DEV_MODE) console.log('Plan Changed:', planinfo);
    },
  },
});
</script>
