<template>
  <q-card flat bordered class="rounded">
    <q-card-section
      :class="$q.screen.gt.sm ? 'q-px-lg q-py-md' : 'q-px-md q-py-sm'"
    >
      <q-toolbar class="text-primary q-pa-none">
        <q-toolbar-title class="text-h6 font-medium">
          Company Profile
        </q-toolbar-title>

        <div v-if="isOrgOwner() || isOrgAdmin()" class="q-gutter-sm">
          <DynamicButton
            v-if="!editing"
            label="Edit Info"
            icon="icon-pencil-02"
            @click="editing = true"
          />

          <DynamicButton
            v-if="editing"
            outline
            :flat="$q.screen.lt.xs || $q.screen.xs"
            label="Cancel"
            icon="icon-x-close"
            @click="cancelEditing"
          />

          <DynamicButton
            v-if="editing"
            label="Save Changes"
            icon="icon-save-01"
            @click="saveOrgInfo"
          />
        </div>
      </q-toolbar>
    </q-card-section>
    <q-separator />
    <q-card-section
      class="q-col-gutter-md font-normal text-primary text-body2"
      :class="$q.screen.gt.sm ? 'q-pa-lg' : 'q-pa-md'"
      :style="editing ? 'max-width: 900px' : ''"
    >
      <div class="row full-wdith items-center justify-start">
        <div class="col-12 col-sm-4 col-md-3 font-medium color-primary2">
          Company Name<span v-if="editing" class="text-red">*</span>
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <q-input
            v-if="editing"
            dense
            outlined
            placeholder="Name"
            v-model="company.title"
            @change="changes_made = true"
          />
          <div v-else class="letter-space-normal">
            {{ selectedOrg.title }}
          </div>
        </div>
      </div>
      <div class="row full-wdith items-center justify-start">
        <div class="col-12 col-sm-4 col-md-3 font-medium color-primary2">
          Company Type<span v-if="editing" class="text-red">*</span>
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <SelectCompanyType
            v-if="editing"
            v-model="company.company_type_id"
            :initModel="company.company_type_id"
            @update:model-value="changes_made = true"
          />
          <div v-else class="letter-space-normal">
            {{ companyTypes[selectedOrg.company_type_id]?.label }}
          </div>
        </div>
      </div>
      <div
        v-if="editing || selectedOrg.about_us"
        class="row full-wdith items-center justify-start"
      >
        <div
          class="self-start col-12 col-sm-4 col-md-3 font-medium color-primary2"
        >
          About Us<span v-if="editing" class="text-red">*</span>
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <q-input
            v-if="editing"
            dense
            outlined
            type="textarea"
            placeholder="About the company..."
            v-model="company.about_us"
            @change="changes_made = true"
          />
          <div v-else class="letter-space-normal">
            {{ selectedOrg.about_us }}
          </div>
        </div>
      </div>
      <div
        v-if="editing || selectedOrg.website"
        class="row full-wdith items-center justify-start"
      >
        <div
          class="col-12 col-sm-4 col-md-3 font-medium color-primary2 font-medium"
        >
          Website
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <InputWebsite
            v-if="editing"
            v-model="company.website"
            @change="changes_made = true"
          />
          <div v-else class="letter-space-normal">
            <external-link :url="selectedOrg.website" />
          </div>
        </div>
      </div>
      <div
        v-if="
          editing ||
          selectedOrg.address_line_1 ||
          selectedOrg.city ||
          selectedOrg.state ||
          selectedOrg.country
        "
        class="row full-wdith items-center justify-start"
      >
        <div
          class="self-start col-12 col-sm-4 col-md-3 font-medium color-primary2 font-medium"
        >
          Address<span v-if="editing" class="text-red">*</span>
        </div>
        <div v-if="editing" class="col-12 col-sm-8 col-md-9">
          <div class="row q-col-gutter-sm items-center justify-start">
            <div class="col-12">
              <q-input
                dense
                outlined
                placeholder="Street Name"
                v-model="company.address_line_1"
                @change="changes_made = true"
              />
            </div>
            <div class="col-12">
              <q-input
                dense
                outlined
                placeholder="City"
                v-model="company.city"
                @change="changes_made = true"
              />
            </div>
            <div class="col-12 col-md-6">
              <q-input
                dense
                outlined
                placeholder="State"
                v-model="company.state"
                @change="changes_made = true"
              />
            </div>
            <div class="col-12 col-md-6">
              <q-input
                dense
                outlined
                placeholder="ZIP Code"
                v-model="company.zipcode"
                @change="changes_made = true"
              />
            </div>
            <div class="col-12">
              <SelectCountry
                v-model="company.country"
                :initModel="company.country"
                @update:model-value="changes_made = true"
              />
            </div>
          </div>
        </div>
        <div
          v-else
          class="col-12 col-sm-8 col-md-9 letter-space-normal q-gutter-sm"
        >
          <div>
            {{ selectedOrg.address_line_1 }}
          </div>
          <div>
            {{ selectedOrg.firstName }}
          </div>
          <div
            v-if="selectedOrg.city || selectedOrg.state || selectedOrg.zipcode"
          >
            {{ selectedOrg.city }}
            {{ selectedOrg.state ? ', ' + selectedOrg.state : '' }}
            {{ selectedOrg.zipcode ? ', ' + selectedOrg.zipcode : '' }}
          </div>
          <div>
            {{ selectedOrg.country }}
          </div>
        </div>
      </div>
      <div
        v-if="
          (selectedOrg.phone_country_code && selectedOrg.phone_number) ||
          editing
        "
        class="row full-wdith items-center justify-start"
      >
        <div class="col-12 col-sm-4 col-md-3 font-medium color-primary2">
          Contact Number<span v-if="editing" class="text-red"></span>
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <InputPhoneNumber
            v-if="editing"
            @areaCodeChanged="
              (e) => {
                company.phone_country_code = e;
                if (e != selectedOrg.phone_country_code) changes_made = true;
              }
            "
            @numberChanged="
              (e) => {
                company.phone_number = e;
                if (e != selectedOrg.phone_number) changes_made = true;
              }
            "
            :areaCode="company.phone_country_code"
            :phoneNumber="company.phone_number"
          />
          <div v-else class="letter-space-normal">
            <external-link
              :url="`tel:+${selectedOrg.phone_country_code}${selectedOrg.phone_number}`"
              :label="`+${selectedOrg.phone_country_code} ${selectedOrg.phone_number}`"
            />
          </div>
        </div>
      </div>
      <div
        v-if="selectedOrg.social_accounts || editing"
        class="row full-wdith items-center justify-start"
      >
        <div
          class="self-start col-12 col-sm-4 col-md-3 font-medium color-primary2 font-medium"
          v-if="
            editing || Object.values(selectedOrg.social_accounts || {})?.length
          "
        >
          Social Media Links
        </div>
        <div class="col-12 col-sm-8 col-md-9 letter-space-normal">
          <div v-if="editing" class="q-col-gutter-sm">
            <template v-for="opt in socialMedia" :key="opt.platform">
              <q-input
                dense
                outlined
                placeholder="company_username"
                v-model="company.social_accounts[opt.platform]"
                @change="changes_made = true"
              >
                <template v-slot:prepend>
                  <div class="text-body2 color-primary2">
                    {{
                      getSocialInfo(opt.platform)
                        ?.base_url.replace('http://', '')
                        .replace('https://', '')
                        .replace('www.', '')
                    }}{{ !opt.platform.endsWith('/') ? '/' : '' }}
                  </div>
                  <q-separator vertical class="q-ml-sm" />
                </template>
              </q-input>
            </template>
          </div>
          <div v-else class="letter-space-normal q-col-gutter-sm">
            <div v-for="link in selectedOrg.social_accounts" :key="link.type">
              <external-link
                :url="getSocialInfo(link.type)?.base_url + '/' + link.username"
              >
                <template v-slot:prepend>
                  <q-icon
                    :name="formatMediaPlatformIconSrc(link.type)"
                    size="xs"
                    class="self-end"
                  />
                </template>
              </external-link>
            </div>
          </div>
        </div>
      </div>
    </q-card-section>

    <!-- Dialog Unsaved Edit -->
    <q-dialog v-model="unsaved">
      <dynamic-dialog-confirmation
        v-bind="$root.messagesDialogs?.editingUnsaved"
        @okay="editing = false"
      />
    </q-dialog>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import {
  extractErrorMessageString,
  objectCopy,
  url2link,
} from 'src/assets/scripts/functions';
import DynamicButton from 'src/components/Helpers/DynamicButton.vue';
import InputWebsite from 'src/components/Helpers/InputWebsite.vue';
import InputPhoneNumber from 'src/components/Helpers/InputPhoneNumber.vue';
import SelectCompanyType from 'src/components/Helpers/SelectCompanyType.vue';
import { mapActions, mapState } from 'pinia';
import useHelpers from 'stores/helpers';
import useUser from 'stores/user';
import useGrowmodo from 'stores/growmodo';
import SelectCountry from '../Helpers/SelectCountry.vue';

export default defineComponent({
  name: 'CompanyProfile',

  components: {
    DynamicButton,
    InputPhoneNumber,
    SelectCompanyType,
    InputWebsite,
    SelectCountry,
  },

  data() {
    return {
      url2link: url2link,
      editing: false,
      unsaved: false,
      company: { social_accounts: {} },
      changes_made: false,
    };
  },

  computed: {
    ...mapState(useHelpers, ['socialMedia']),
    ...mapState(useUser, ['user', 'selectedOrg']),
    ...mapState(useGrowmodo, ['companyTypes']),
  },

  watch: {
    editing: {
      async handler(val) {
        if (val) {
          // Editing
          // Update the editing value
          this.company = objectCopy(this.selectedOrg);
          this.company.social_accounts = {};
          this.selectedOrg.social_accounts.forEach((e) => {
            this.company.social_accounts[e.type] = e.username;
          });
          if (!this.company.social_accounts) this.company.social_accounts = {};
          this.company.triggers = 2;
        } else {
          // Viewing
          // Reset company info
          this.company = {};
          this.changes_made = false;
        }
      },
    },
  },

  methods: {
    ...mapActions(useUser, ['updateOrganization', 'isOrgOwner', 'isOrgAdmin']),
    ...mapActions(useHelpers, ['getSocialInfo', 'formatMediaPlatformIconSrc']),

    async saveOrgInfo() {
      if (!this.changes_made) {
        this.editing = false;
        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: 'No changes has been made',
        });
        return;
      }
      const _ = this.company || {};
      const payload = {
        title: _.title,
        address_line_1: _.address_line_1,
        state: _.state,
        company_type_id: _.company_type_id,
        about_us: _.about_us,
        city: _.city,
        zipcode: _.zipcode,
        country: _.country,
      };
      if (
        (_.phone_number && !_.phone_country_code) ||
        (!_.phone_number && _.phone_country_code)
      ) {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: _.phone_number
            ? 'Phone country code is required'
            : 'Phone number is required',
        });
        return;
      }
      if (_.phone_number) payload.phone_number = _.phone_number;
      if (_.phone_country_code)
        payload.phone_country_code = _.phone_country_code;
      if (_.address_line_2) payload.address_line_2 = _.address_line_2;
      if (_.website) payload.website = _.website;
      if (_.social_accounts && Object.keys(_.social_accounts)?.length) {
        payload.social_accounts = {};
        Object.keys(_.social_accounts).forEach((platform) => {
          if (_.social_accounts[platform])
            payload.social_accounts[platform] =
              _.social_accounts[platform] || '';
        });
      }
      const response = await this.updateOrganization(payload);
      if (response.success) {
        this.editing = false;
        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: 'Your changes have been successfully saved',
        });
      } else {
        const message = extractErrorMessageString(response?.message);
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: message || 'Error updating organization',
        });
      }
    },

    cancelEditing() {
      if (this.changes_made) {
        this.unsaved = true;
      } else {
        this.editing = false;
      }
    },
  },
});
</script>
