<template>
  <q-input
    dense
    outlined
    mask="(###) ### - ####"
    v-model="account.phone.number"
  >
    <template v-slot:prepend>
      <q-select
        dense
        emit-value
        borderless
        :use-input="allowSearch && areaCodeSearch"
        class="no-border small-innersearch"
        v-model="account.phone.code"
        popup-content-class="shadow-ui"
        :display-value="`${
          account.phone.code && areaCodes?.[account.phone.code]
            ? areaCodes?.[account.phone.code]?.label
            : 'Opt.'
        }`"
        :options="
          allowSearch && areaCodeSearch ? areaCodeOptions : limitedAreaCodes
        "
        :loading="areaCodeOpt.loading"
        @filter="filterFn"
        @focus="areaCodeSearch = true"
        @blur="areaCodeSearch = false"
        @virtual-scroll="scrollFn"
      />
      <span class="text-body2 q-pl-sm">
        {{ account.phone.code ? '+' + account.phone.code : '' }}
      </span>
    </template>
  </q-input>
</template>

<script>
import { defineComponent, nextTick } from 'vue';
import { mapState } from 'pinia';
import useHelpers from 'stores/helpers';

export default defineComponent({
  name: 'InputPhoneNumber',

  emits: ['areaCodeChanged', 'numberChanged'],

  props: {
    areaCode: {
      type: [String, Number],
      default: 1,
    },
    phoneNumber: {
      type: [String, Number],
      default: '',
    },
    allowSearch: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      areaCodeOptions: [],
      account: {
        phone: {
          code: 1,
          number: '(555) 000 - 0001',
        },
      },
      areaCodeSearch: false,
      areaCodeOpt: {
        loading: false,
        pageSize: 50,
        nextPage: 2,
      },
    };
  },

  mounted() {
    this.account.phone = {
      code: this.areaCode,
      number: this.phoneNumber,
    };
  },

  computed: {
    ...mapState(useHelpers, ['areaCodes']),

    areaCodesArray() {
      return Object.values(this.areaCodes);
    },

    limitedAreaCodes() {
      return this.areaCodesArray.slice(
        0,
        this.areaCodeOpt?.pageSize * (this.areaCodeOpt?.nextPage - 1)
      );
    },

    areaCodesLastPage() {
      return Math.ceil(this.areaCodesArray.length / this.areaCodeOpt?.pageSize);
    },
  },

  watch: {
    ['account.phone.code']: {
      async handler(val) {
        this.areaCodeSearch = false;
        this.$emit('areaCodeChanged', val);
      },
    },
    ['account.phone.number']: {
      async handler(val) {
        this.$emit('numberChanged', val);
      },
    },
  },

  methods: {
    filterFn(val, update) {
      if (val === '') {
        update(() => {
          this.areaCodeOptions = this.areaCodesArray;
        });
        return;
      }

      update(() => {
        const needle = val.toLowerCase();
        this.areaCodeOptions = this.areaCodesArray.filter((v) => {
          let res = v?.label?.toLowerCase().includes(needle);
          if (!res) res = v?.name?.toLowerCase().includes(needle);
          return res;
        });
      });
    },

    scrollFn({ to, ref }) {
      const lastIndex = this.limitedAreaCodes.length - 1;
      if (
        this.areaCodeOpt.loading !== true &&
        this.areaCodeOpt.nextPage < this.areaCodesLastPage &&
        to === lastIndex
      ) {
        this.areaCodeOpt.loading = true;

        setTimeout(() => {
          this.areaCodeOpt.nextPage++;
          nextTick(() => {
            ref.refresh();
            this.areaCodeOpt.loading = false;
          });
        }, 500);
      }
    },
  },
});
</script>
