    };
  },

  methods: {
    ...mapActions(useUser, ['logout']),

    logoutAccount() {
      this.logout();
      this.$router.replace('/auth');
    },

    start(payload) {
      this.$emit('start', payload);
      if (this.formStep < 2) this.formStep++;
    },

    prevButtonClicked() {
      this.$refs?.['formStepper']?.previous?.();
    },

    nextButtonClicked() {
      this.$refs?.['formStepper']?.next?.();
    },

    submitFormAction() {
      // Initialize
      this.offboard_submitting = true;
      // Done
      this.nextButtonClicked();
      this.offboarded = true;
      // Safe reset
      setTimeout(() => {
        this.offboard_submitting = false;
      }, 1500);
    },

    backToHome() {
      this.$router.push('/');
      this.$emit('exit');
    },

    pauseAccount() {
      this.formStep = 'pause_done';
    },

    switchToMaintenance() {
      this.formStep = 'maintenance_done';
    },

    requestMaintenanceTask() {
      if (process.env.DEV) console.log('Maintenance Task Requested');
      this.$emit('exit');
    },

    cancelAccount() {
      let emsg = '';
      switch (false) {
        case !!this.cancel?.reason:
          emsg = 'Reason for cancellation is not provided';
          break;
        case !!this.cancel?.q1 && !!this.cancel?.q2 && !!this.cancel?.q3:
          emsg = 'Please confirm you account cancellation';
          break;
        default:
          break;
      }
      if (emsg) {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: emsg,
        });
      } else {
        if (process.env.DEV) console.log('Account canceled.');
        this.formStep = 'cancel_done';
        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: 'Account has been canceled',
        });
        setTimeout(() => {
          this.logoutAccount();
        }, 1500);
      }
    },

    stopCancelProcess() {
      if (process.env.DEV) console.log('Changed of mind.');
      this.$emit('exit');
    },
  },

  beforeUnmount() {
    this.$emit('exit');
  },
});
</script>
