<template>
  <q-page padding>
    <q-layout container view="LHh lpR lFf" class="absolute-full bg-color">
      <q-drawer
        bordered
        show-if-above
        side="left"
        class="drawer-settings bg-white"
        :width="250"
        :breakpoint="600"
        v-model="$root.settingsDrawer"
      >
        <q-scroll-area class="fit">
          <q-list class="q-pt-lg">
            <div class="flex flex-center">
              <q-item
                class="menu-item text-primary text-body1 font-medium"
                style="height: 56px"
              >
                <q-item-section>Dashboard</q-item-section>
              </q-item>
            </div>

            <div class="flex flex-center">
              <q-item
                clickable
                v-ripple:secondary
                v-for="(item, i) in settingsMenu"
                :key="i"
                class="menu-item text-body2 rounded font-medium q-my-xs hover-text-secondary hover-bg-accent"
                :class="
                  tab === item.key
                    ? 'text-secondary font-medium bg-accent'
                    : 'text-primary hover-bg-accent'
                "
                :to="
                  item.noswitch !== true ? '/settings/' + item.key : undefined
                "
                @click="
                  () => {
                    // Switch
                    if (item.noswitch !== true) {
                      tab = item.key;
                    }
                    // Perform Action
                    if (item.action) item.action();
                  }
                "
              >
                <q-item-section avatar class="q-pr-sm" style="min-width: unset">
                  <q-avatar rounded size="sm" class="rounded">
                    <q-icon
                      :name="item.icon"
                      size="sm"
                      class=""
                      :class="
                        tab === item.key ? 'text-secondary' : 'color-primary2'
                      "
                    />
                  </q-avatar>
                </q-item-section>
                <q-item-section>{{ item.label }}</q-item-section>
                <q-item-section side v-if="item.badge">
                  <q-badge
                    rounded
                    :color="item.badge?.color"
                    :class="
                      item.badge?.class +
                      ' ' +
                      (tab === item.key
                        ? item.badge?.active_class
                        : item.badge?.inactive_class)
                    "
                  >
                    {{ item.badge?.message }}
                  </q-badge>
                </q-item-section>
              </q-item>
            </div>
          </q-list>
        </q-scroll-area>
      </q-drawer>

      <q-page-container>
        <q-page class="container">
          <q-tab-panels
            v-model="tab"
            animated
            transition-prev="fade"
            transition-next="fade"
            class="bg-transparent"
          >
            <q-tab-panel name="profile">
              <AccountProfile />
            </q-tab-panel>

            <q-tab-panel name="company_profile">
              <CompanyProfile />
            </q-tab-panel>

            <q-tab-panel name="users">
              <UsersList />
            </q-tab-panel>

            <q-tab-panel name="subscription">
              <SubscriptionInformation />
            </q-tab-panel>
          </q-tab-panels>

          <!-- Dialog Confirm Logout -->
          <q-dialog v-model="confirmLogout">
            <dynamic-dialog-confirmation
              v-bind="$root.messagesDialogs?.confirmLogout"
              @okay="this.logoutAccount()"
            />
          </q-dialog>
        </q-page>
      </q-page-container>
    </q-layout>
  </q-page>
</template>

<script>
import { defineComponent } from 'vue';
import AccountProfile from 'src/components/Accounts/Profile.vue';
import CompanyProfile from 'src/components/Accounts/Company.vue';
import SubscriptionInformation from 'src/components/Accounts/Subscription.vue';
import UsersList from 'src/components/Users/UsersList.vue';
import { mapActions, mapState } from 'pinia';
import useGrowmodo from 'stores/growmodo';
import useUser from 'stores/user';

export default defineComponent({
  name: 'AccountSettingsPage',

  components: {
    AccountProfile,
    CompanyProfile,
    SubscriptionInformation,
    UsersList,
  },

  data() {
    const settingsMenu = {
      profile: {
        key: 'profile',
        label: 'Profile',
        icon: 'icon-user-03',
      },
      company_profile: {
        key: 'company_profile',
        label: 'Company Profile',
        icon: 'icon-building-05',
      },
      users: {
        key: 'users',
        label: 'Users',
        icon: 'icon-users-02',
      },
      subscription: {
        key: 'subscription',
        label: 'Subscription',
        icon: 'icon-wallet-02',
      },
      billing: {
        key: 'billing',
        noswitch: true,
        label: 'Billing',
        icon: 'icon-credit-card-02',
        action: this.goToBilling,
      },
      logout: {
        key: 'logout',
        noswitch: true,
        label: 'Logout',
        icon: 'icon-log-out-01',
        action: () => {
          this.confirmLogout = true;
        },
      },
    };
    return {
      title: 'Account Settings',
      tab: '',
      settingsMenu,
      editing: false,
      confirmLogout: false,
    };
  },

  async mounted() {
    this.$root.setMeta({ title: this.title });
    this.$root.toggleSettingsDrawer(true);
    let routeTab = this.$route.params?.settings_tab;
    this.tab = this.settingsMenu.profile?.key;
    if (routeTab) {
      let menu = this.settingsMenu[routeTab]?.key;
      if (menu) this.tab = routeTab;
    }
  },

  watch: {
    tab: {
      async handler(val) {
        this.$root.setMeta({
          title: this.settingsMenu?.[val]?.label || 'Settings',
        });
      },
    },
  },

  computed: {
    ...mapState(useGrowmodo, ['redirect']),
  },

  methods: {
    ...mapActions(useUser, ['logout']),

    async logoutAccount() {
      const response = await this.logout();
      if (!response.success) {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: 'An error occured during logout',
        });
      }
    },

    goToBilling() {
      this.$root.toggleRedirectConfirmation(this.redirect?.stripe);
    },
  },

  beforeUnmount() {
    this.$root.toggleSettingsDrawer(false);
  },
});
</script>
